<template>
  <div class="counter-cpms">
    <img src="../assets/logo.png" alt="">
    <h3> {{ count }} </h3>
    <button @click="increase"> increase </button>
    <button @click="decrease"> decrease </button>
  </div>
</template>

<script>
  export default {
    name: 'CounterCmp',
    data() {
      return {
        count: 0
      }
    },
    methods: {
      increase () {
        this.count += 1
      },
      decrease () {
        this.count -= 1
      }
    },
  }
</script>

<style lang="scss" scoped>
.counter-cpms {
  border: 5px solid lightgoldenrodyellow;
  padding: 30px;
}
</style>